.profile__avatar-photo{
    width: 100%;
    height: 100%;
    align-self: center;
    border-radius: 50%;
    object-fit: cover;
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
@media all and (max-width: 580px){
    .profile__avatar{
        margin: 3.75px auto 0;
    }
}